/* MF
========================================================*/
.mailform {
  position: relative;
  text-align: left;
  margin-left: auto;
  margin-right: auto;
}
.mailform input, .mailform select, .mailform textarea {
  padding: 14px 18px;
}
.mailform input:-moz-placeholder, .mailform select:-moz-placeholder, .mailform textarea:-moz-placeholder {
  color: #888;
  opacity: 1;
}
.mailform input::-webkit-input-placeholder, .mailform select::-webkit-input-placeholder, .mailform textarea::-webkit-input-placeholder {
  color: #888;
  opacity: 1;
}
.mailform input::-moz-placeholder, .mailform select::-moz-placeholder, .mailform textarea::-moz-placeholder {
  color: #888;
  opacity: 1;
}
.mailform input:-ms-input-placeholder, .mailform select:-ms-input-placeholder, .mailform textarea:-ms-input-placeholder {
  color: #888;
  opacity: 1;
}

/* MF Input
========================================================*/
.mfInput input:-webkit-autofill ~ .mfIcon span:before,
.mfInput textarea:-webkit-autofill ~ .mfIcon span:before {
  color: #2D2D2D;
}

/* MF Controls
========================================================*/
.mfControls {
  word-spacing: 10px;
  text-align: left;
}
.mfControls > * {
  word-spacing: normal;
  margin-bottom: 5px;
  min-width: 200px;
  width: auto;
  font-family: inherit;
  font-size: 17px;
  letter-spacing: -0.5px;
  transition: 0.3s all ease;
}
@media (max-width: 767px) {
  .mfControls > * {
    width: 100%;
  }
}

/* Mail Form PlaceHolder
========================================================*/
.mfPlaceHolder {
  transition: 0.3s all ease;
  font: inherit;
  cursor: text;
  position: absolute;
  left: 10px;
  top: 0;
  padding: 14px 18px;
  line-height: 26px;
  color: #888;
  opacity: 1;
}
*:-webkit-autofill ~ .mfPlaceHolder {
  opacity: 1;
  transform: translateY(-30%);
  font-size: 14px;
}
.mfPlaceHolder.state-1 {
  opacity: 1;
  transform: translateY(-30%);
  font-size: 14px;
}

/* Mail Form Validation
  ========================================================*/
.mfValidation {
  transform-origin: 0% 50%;
  transition: 0.3s all ease;
  -webkit-animation: notifanim-fo 0.4s cubic-bezier(0.55, 0, 0.1, 1) forwards;
  animation: notifanim-fo 0.4s cubic-bezier(0.55, 0, 0.1, 1) forwards;
  box-shadow: 2px 2px 2px 0 rgba(0, 0, 0, 0.2);
  background: #888;
  position: absolute;
  width: 210px;
  margin-left: -105px;
  padding: 10px;
  height: 40px;
  line-height: 20px;
  opacity: 0;
  visibility: hidden;
  cursor: pointer;
  bottom: -30px;
  right: 0px;
  font-weight: 300;
  font-size: 12px;
  color: #FFF;
  z-index: 999;
}
.mfValidation:hover {
  background: #6f6f6f;
}
.mfValidation:before {
  content: '';
  position: absolute;
  bottom: 100%;
  right: 10%;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 4px 5px 4px;
  border-color: transparent transparent #888 transparent;
}
.mfValidation:hover:before {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 4px 5px 4px;
  border-color: transparent transparent #6f6f6f transparent;
}
.mfValidation.error {
  transform: scale(1);
  opacity: 1;
  visibility: visible;
  -webkit-animation: notifanim 0.4s cubic-bezier(0.55, 0, 0.1, 1) forwards;
  animation: notifanim 0.4s cubic-bezier(0.55, 0, 0.1, 1) forwards;
}
@media (max-width: 767px) {
  .mfValidation {
    text-align: right;
    top: auto;
    left: auto;
    top: -20px;
    right: 0;
    background: none;
    padding: 0;
    margin: 0 0 3px;
    min-height: 0;
    color: #DA7B77;
    box-shadow: none;
  }
  .mfValidation:before {
    display: none;
  }
  .mfValidation:hover {
    background: none;
    right: 5px;
  }
}
@-webkit-keyframes notifanim-fo {
  0% {
    opacity: 1;
    visibility: visible;
    transform: scale(1);
  }
  20% {
    opacity: 1;
    transform: scale(1.1);
  }
  99% {
    visibility: hidden;
  }
  100% {
    visibility: hidden;
    transform: scale(0.3);
    opacity: 0;
  }
}
@keyframes notifanim-fo {
  0% {
    opacity: 1;
    visibility: visible;
    transform: scale(1);
  }
  20% {
    opacity: 1;
    transform: scale(1.1);
  }
  99% {
    visibility: hidden;
  }
  100% {
    visibility: hidden;
    transform: scale(0.3);
    opacity: 0;
  }
}
@-webkit-keyframes notifanim {
  0% {
    opacity: 0;
    visibility: hidden;
    transform: scale(0.3);
  }
  1% {
    visibility: visible;
  }
  50% {
    transform: scale(1);
  }
  75% {
    transform: scale(0.9);
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
@keyframes notifanim {
  0% {
    opacity: 0;
    visibility: hidden;
    transform: scale(0.3);
  }
  1% {
    visibility: visible;
  }
  50% {
    transform: scale(1);
  }
