.preloader {
  position: fixed;
  left: 0;
  top: 0;
  z-index: -99999;
  width: 100%;
  height: 100%;
  overflow: visible;
  background: #fff;
  display: none;
  text-align: center;
}

.loader {
  display: table-cell;
  vertical-align: middle;
  position: relative;
  width: 200px;
  height: 200px;
}

.loader-icon {
  width: 80px;
  height: 80px;
  border: 5px solid #232323;
  border-right-color: #eee;
  border-radius: 50%;
  position: relative;
  -webkit-animation: loader-rotate 1s linear infinite;
          animation: loader-rotate 1s linear infinite;
  margin: 0 auto;
}

@-webkit-keyframes loader-rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes loader-rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}


.html {
  width: 90%;
  -webkit-animation: html 2s;
          animation: html 2s;
}

@-webkit-keyframes html {
  0% {
    width: 0%;
  }

  100% {
    width: 90%;
  }
}

@keyframes html {
  0% {
    width: 0%;
  }

  100% {
    width: 90%;
  }
}

.css {
  width: 60%;
  -webkit-animation: css 2s;
          animation: css 2s;
}

.javascript {
  width: 80%;
  -webkit-animation: javascript 2s;
          animation: javascript 2s;
}

.react {
  width: 80%;
  -webkit-animation: react 2s;
          animation: react 2s;
}

@-webkit-keyframes css {
  0% {
    width: 0%;
  }

  100% {
    width: 60%;
  }
}

@keyframes css {
  0% {
    width: 0%;
  }

  100% {
    width: 60%;
  }
}
@-webkit-keyframes javascript {
  0% {
    width: 0%;
  }

  100% {
    width: 80%;
  }
}
@keyframes javascript {
  0% {
    width: 0%;
  }

  100% {
    width: 80%;
  }
}
@-webkit-keyframes react {
  0% {
    width: 0%;
  }

  100% {
    width: 80%;
  }
}
@keyframes react {
  0% {
    width: 0%;
  }

  100% {
    width: 80%;
  }
}

