.html {
  width: 90%;
  animation: html 2s;
}

@keyframes html {
  0% {
    width: 0%;
  }

  100% {
    width: 90%;
  }
}

.css {
  width: 60%;
  animation: css 2s;
}

.javascript {
  width: 80%;
  animation: javascript 2s;
}

.react {
  width: 80%;
  animation: react 2s;
}

@keyframes css {
  0% {
    width: 0%;
  }

  100% {
    width: 60%;
  }
}
@keyframes javascript {
  0% {
    width: 0%;
  }

  100% {
    width: 80%;
  }
}
@keyframes react {
  0% {
    width: 0%;
  }

  100% {
    width: 80%;
  }
}
