.section-title {
  position: relative;
  padding-bottom: 20px;
  margin-bottom: 0;
}

.section-title:after {
  content: "";
  width: 60px;
  height: 2px;
  background: rgba(0, 0, 0, 0.6);
  position: absolute;
  bottom: 0;
  left: calc(50% - 30px);
}

.section-title:before {
  content: "";
  width: 100%;
  height: 1px;
  background: rgba(0, 0, 0, 0.2);
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: 250px;
}

.section-title.style2:after {
  content: "";
  width: 50px;
  height: 2px;
  background: rgba(0, 0, 0, 0.6);
  position: absolute;
  bottom: 0;
  left: calc(50% - 30px);
}

.section-title.style2:before {
  content: none;
}

.section-title.white {
  color: #fff;
}

.section-title.white:after {
  background: rgba(255, 255, 255, 0.6);
}

.section-title.white:before {
